import { newSpecPage } from '@stencil/core/testing';
import { PrestoPlayer } from '../presto-player';

describe('presto-player', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoPlayer],
      html: `<presto-player video_id="1"></presto-player>`,
    });
    expect(page.root).toMatchSnapshot();
  });

  it('audio renders', async () => {
    const page = await newSpecPage({
      components: [PrestoPlayer],
      html: `<presto-player video_id="1" provider='audio' ></presto-player>`,
    });
    expect(page.root).toMatchSnapshot();
  });
});
